* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family:'Times New Roman', Times, serif;
}

.nav {
  display: flex;
  /* justify-content: space-between; */
  align-items: center;
  padding: 10px 20px;
  background-color: white;
  color: white;
  /* border-bottom: 2px solid rgb(29,58,255); */
  box-shadow: 0 -2px 16px rgb(29,58,255);
  position: fixed;
  width: 100%;
  z-index: 13;
}
.nav img{
  width: 4rem;
  margin-right: 1rem;
  margin-left: 1rem;
  /* padding: 0 1rem; */
}
.navbar-links{
  display: flex;
  justify-content: space-between;
}
/* .main-links{
  margin-left: -10rem;
} */
.bn26{
  margin-left: 50%;
}
.site-title {
  font-size: 1.5em;
  color: white;
  text-decoration: none;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
  width: 100%;
  justify-content: space-between;
}
.main-links{
  display: flex;
  flex-direction: row;
}
.nav-links li {
  padding: 10px;
}
.nav-links a {
  color: black;
  text-decoration: none;
  font-size: 1.2rem;
}

.nav-links a:hover,
.nav-links .active a {
  color: rgb(29,58,255);
}
.bn632-hover {
  width: 160px !important;
  font-size: 16px !important;
  font-weight: 600 !important;
  color: #fff !important;
  cursor: pointer ;
  margin: 20px ;
  height: 55px !important;
  text-align:center !important;
  border: none !important;
  background-size: 300% 100% !important;
  border-radius: 50px !important;
  /* moz-transition: all .4s ease-in-out ; */
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.bn632-hover:hover {
  background-position: 100% 0;
  /* moz-transition: all .4s ease-in-out; */
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.bn632-hover:focus {
  outline: none;
}

.bn632-hover.bn26 {
  background-image: linear-gradient(
    to right,
    #0f54c1,
    #4481eb,
    #04befe,
    #3f86ed
  );
  box-shadow: 0 4px 15px 0 rgba(65, 132, 234, 0.75);
  padding: 1rem 1.5rem;
}
.menu-toggle {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
  flex-direction: column;
  gap: 5px;
  width: 100%;
  align-items: end;
}

.menu-toggle .bar {
  width: 25px;
  height: 3px;
  background-color: rgb(29,58,255);
}

/* Responsive styles */
@media (max-width: 768px) {
  .nav-links {
    display: none;
    flex-direction: column;
    gap: 0;
    position: absolute;
    top: 82px;
    right: 0;
    background-color: rgb(29,58,255);
    width: 100%;
    text-align: center;
    color: #fff;
    height: 20rem
  }
  .main-links{
    display: flex;
    flex-direction: column;
  }
  .nav-links {
    justify-content: flex-start;
  }
  .nav img{
    margin-left: 0;
  }
  /* .nav-links a:hover,
.nav-links .active a {
  color: black;
} */

  /* .navbar-links{
    width: 100%;
    display: flex;
    justify-content: space-between;
  } */

  .nav-links a{
    color: #fff;
  }
  .nav-links.open {
    display: flex;
  }
  .menu-toggle {
    display: flex;
  }

  .nav-links li {
    padding: 15px 0;
  }
  .nav-links a:hover, .nav-links .active a{
    color: black;
  }

}
.logo {
  width: 100px; 
  height: auto;
  object-fit: contain;
}

/* --------------Home page -------------------*/
.hero{
  display: flex;
  flex-direction: row;
}
.logo-section{
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  width: 40%;
}
.logo-section img{
  width: 95%;
  margin-top: 4rem;
}
.logo-section img{
  
  transition: transform 0.3s ease-in-out; /* Smooth transition */
}

.logo-section:hover img{
  transform: scale(1.05); /* Scale the image to 110% on hover */
}
.hero-content{
  width: 60%;
  padding-left: 2rem;
}
.hero-content h4{
  font-size: 1.5rem;
  color: rgb(29,58,255);
  padding: 150px 5px 0 0;
  font-weight: 400;
}
.hero-content h1{
  font-size: 3rem;
  padding: 0px  0 10px 0;
  font-weight: 400;
}
.hero-content h3{
  font-size: 1.6rem;
  color: rgb(29,58,255);
  margin-top: 0;
  padding: 20px 0 20px  0;
  font-weight: 400;
}
.hero-content p{
  line-height: 1.5;
  padding: 10px  0 30px 0;
}
.btn-quote{
  position: relative;
  display: inline-flex;
  width: 180px;
  height: 55px;
  perspective: 1000px;
  margin-bottom: 100px;
}
.btn-quote a{
  font-size: 19px;
  letter-spacing: 1px;
  transform-style: preserve-3d;
  transform: translateZ(-25px);
  transition: transform .25s;
  font-family: 'Montserrat', sans-serif;
  
}
.btn-quote a:before,
.btn-quote a:after{
  position: absolute;
  content: "Get a Quote";
  height: 55px;
  width: 180px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 5px solid rgb(29,58,255);
  box-sizing: border-box;
  border-radius: 10px;
}
.btn-quote a:before{
  color: #fff;
  background: rgb(29,58,255);
  transform: rotateY(0deg) translateZ(25px);
}
.btn-quote a:after{
  color: rgb(29,58,255);
  transform: rotateX(90deg) translateZ(25px);
}
.btn-quote a:hover{
  transform: translateZ(-25px) rotateX(-90deg);
}

/* -----------------Our Services---------------- */
.services{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.services-section{
  text-align: center;
}
.line {
  width: 100%; 
  height: 2px;
  background-color: rgb(29,58,255); 
  margin: 4px auto;
}
.services-section h1{
  font-size: 3.5rem;
  color: rgb(29,58,255);
  font-weight: 400;
}
.all-services {
  display: flex; 
  justify-content: center; 
  align-items: stretch;
  flex-wrap: wrap;
  margin: 20px 0;
  justify-content: space-around;
}

.service-box {
  position: relative;
  width: 350px; 
  padding: 40px;
  background: linear-gradient(to bottom, #E4E5EC, #dae3fc);
  box-shadow: 0 8px 12px rgba(0, 0, 0.8, 0.6);
  border-radius: 15px;
  margin: 20px;
  box-sizing: border-box;
  overflow: hidden;
  text-align: center;
}

.service-box:before {
  content: '';
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, .2);
  z-index: 2;
  pointer-events: none;
  text-align: center;
}

.service-box .icon {
  position: relative;
  width: 80px;
  height: 80px;
  color: #fff;
  background: rgb(29,58,255);
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 55px auto;
  border-radius: 20%;
  font-size: 50px;
  font-weight: normal;
  transition: 2s;
  padding: 10px;
}

.service-box .icon i {
  padding: 12px;
}

.service-box:hover .icon {
  box-shadow: 0 0 0 400px rgb(29,58,255);
}
.content{
  height: 10rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  
}
.service-box .content {
  position: relative;
  z-index: 1;
  transition: 1.5s;
}

.service-box:hover .content {
  color: #fff;
}

.service-box .content h3 {
  font-size: 20px;
  margin: 10px 0;
  padding: 0;
  text-align: center;
}

.service-box .content p {
  margin: 0;
  padding: 0;
  text-align: center;
}

.content a {
  display: inline-block;
  padding: 10px 25px;
  background: #fff;
  border-radius: 10px;
  text-decoration: none;
  color: #000;
  font-weight: 500;
  margin-top: 20px;
  box-shadow: 5px 2px 5px rgba(0, 0, 0, .2);
  border: 1px solid rgb(29,58,255);
  width: 8rem;
  align-self: center;
}



/* images section */
/* ImageSection.css */
.image-section {
  padding: 50px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background-color: #f9f9f9; */
}

.image-container {
  display: flex;
  justify-content: space-around; /* Ensures all images are spaced evenly */
  align-items: center;
  width: 100%;
  margin-top: 4rem;
  
  flex-wrap: wrap;
}

.hover-image {
  width: 200px; /* Initial image size */
  height: 200px;
  margin-bottom: 4rem;
  transition: transform 0.3s ease-in-out; /* Smooth animation on hover */
  border-radius: 10px;
  box-shadow: 0 12px 16px rgba(0, 0, 0.8, 0.6); /* Optional: adds a border-radius for a rounded effect */
}

.hover-image:hover {
  transform: scale(1.2); /* Makes the image grow 20% larger on hover */
}


/* -------------insightful section-------------------- */
.insightful-section{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


/* Styling for the box */
.all-insight{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  justify-content: space-around;
}
.insights{
  display: flex;
  justify-content: flex-start;
  /* align-items: flex-start; */
  width: 25rem;
  height: 30rem;
  background-color: #3f86ed;
  border-radius: 20px;
  flex-direction: column;
  color: #fff;
  text-align: center;
  font-weight: 400;
  margin-top: 5rem;
}
.insight-img{
  display: flex;
  justify-content: center;
  margin-top: 2.5%;
  width: 100%;
  height: 70%;
  /* align-items: flex-start; */
}
.insight-image {
  transition: transform 0.3s ease-in-out; /* Smooth transition */
}

.insight-image:hover {
  transform: scale(1.05); /* Scale the image to 110% on hover */
}


.insight-img img{
  width: 95%;
  border-radius: 20px;
}
.text-box h2{
  margin-top: 3%;

}
.insights-a{
  margin-top: -20px;
}
.text-box h2{
   font-weight: 400;
}



