/* animation test 2 */
/* Default hidden state for sections */
section {
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 0.5s ease-out, transform 0.5s ease-out;
}

/* Hero section animation */
.hero.animate-slide-in {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 0.8s ease-out, transform 0.8s ease-out;
}

/* Services section animation */
.services.animate-fade-in-up {
  opacity: 1;
  transform: translateY(0);
}

/* Slide up animation for service boxes */
.service-box {
  opacity: 0;
  transform: translateY(100px);
  transition: opacity 0.7s ease-out, transform 1.5s ease-out;
}

.service-box.slide-up {
  opacity: 1;
  transform: translateY(0);
}

.service-box.slide-up-delay {
  transition-delay: 0.3s;
  opacity: 1;
  transform: translateY(0);
}

/* Highlighted section animation */
.image-section.animate-zoom-in {
  opacity: 1;
  transform: scale(1);
  transition: opacity 0.7s ease-out, transform 3.5s ease-out;
}

/* Insightful section animation */
.insightful-section.animate-slide-in-from-left {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 0.9s ease-out, transform 2.7s ease-out;
}

.insightful-section {
  opacity: 0;
  transform: translateX(-80px);
}
