.check{
    padding-top: 16rem;
}
.ch{
    border: 1rem solid black;
    border-radius: 50px;
    background-color: blueviolet;
}

.content-dev{
    padding-top: 4rem;
}

/* .etihad{
    padding-top: 15rem;
