 .main-content-div{
    position: relative;
    width: 100%;
    height:85vh;

    display: flex;
    justify-content: center;
    align-items: center;




    background-image: url('../assets/images/services.jpg'); 
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}


/* .check{
    padding: 13rem;
} */
.main-content-div::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 1;
}
.content-dev {
    position: relative;
    color: white;
    text-align: center; 
    text-shadow: 4px 3px 4px rgb(29,58,255/ 50%);
    z-index: 3;
}
.content-dev h1 {
    font-size: 60px;
    text-transform: uppercase;
    font-weight: 400;
    line-height: 1;
    color: #fff;
}
.content-dev h1::first-letter {
    font-size: 100px;
    color: #fff;
}
.content-dev-40 span {
    font-size: 100px;
    color: #e4a73f;
}
.content-dev p {

    font-size: 18px;
    padding: 0 15px 30px 15px;
}





.projects-dev{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.dev-text{
    background-color:rgb(29,58,255) !important;
    display: flex;
    position: absolute;
    font-size: 4;
    width: 60%;
    padding: 40px;
    justify-content: center;
    align-items:center;
    border-radius: 30px;
    text-align: center;
    z-index: 1;
    
}
.dev-text p{
    font-family: 'Times New Roman', Times, serif;
    color: #e8f3f3 !important;
    font-size: 1.1rem;
    line-height: 1.6;
    font-weight: 500;
    display: flex;
    align-self: center;
    justify-self: center;
    margin-bottom:0 !important;
    
}
.new-section-services {
    padding-top: 14rem;
}



@media (max-width: 768px) {
    .content-dev h1 {
        font-size: 2rem;
    }
    
    .content-dev p {
        
        font-size: 1rem;
    }
}
@media (max-width: 565px) {
    .content-dev h1 {
        font-size: 1.5rem;
    }

    .content-dev-40 span {
        font-size: 60px;
    }
    
    .content-dev p {
        font-size: 0.875rem;
        padding-top: 20px;
    }
    .content-dev h1::first-letter {
        font-size: 60px;
    }
}
@media screen and (max-width: 830px) {
    .devs-text{
        width: 80%;
    }
}
@media screen and (max-width: 630px) {
    .dev-text{
        width: 90%;
        padding: 20px;
        line-height: 1;
        margin-top: 0;
    }
    .dev-text p{
        line-height: 1.2;
    }
}
@media screen and (max-width: 430px) {
    .dev-text{
        padding: 13px;
    }
    .dev-text p{
        font-size: 1rem;
        line-height: 1;
    }
} 

