*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-family: 'Times New Roman', Times, serif;
}
 .main-content-div-seo{
    position: relative;
    width: 100%;
    height:75vh;
    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url('../../assets/images/services.jpg'); 
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.main-content-div-seo::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(1, 16, 82, 0.671);
    z-index: 1;
}
.content-dev-seo {
    position: relative;
    color: white;
    text-align: center; 
    text-shadow: 4px 3px 4px rgb(29,58,255/ 50%);
    z-index: 3;
}
.content-dev-seo h1 {
    font-size: 42px;
    text-transform: uppercase;
    font-weight: 400;
    line-height: 1;
    color: #fff;
    padding-top: 3rem;
}
.content-dev-seo h1::first-letter {
    font-size: 66px;
    color: #fff;
}
.content-dev-seo p{
    font-size: 24px;
    padding: 1rem 7rem;
}
.content-dev-seo span{
    color: black;
    font-weight: 100;
}
.main-seo{
    margin-top: 3rem;
}
.main-seo a {
    text-decoration: none;
    border: 3px solid white;
    padding: 0.8rem 1.5rem;
    border-radius: 50px;
    color: #fff;
    font-size: 24px;
    position: relative;
    background-color: transparent;
    background-image: linear-gradient(to right, #3949AC 0%, #3949AC 100%);
    background-size: 0% 100%;
    background-repeat: no-repeat;
    transition: color 0.4s ease, background-size 0.6s ease;
    overflow: hidden;
}

.main-seo a:hover {
    background-size: 100% 100%; /* Expands background from left to right */
    color: #fff;
}


/* 2nd section */
.main-content-services {
    display: flex;
    width: 100%;
    flex-direction: row;
    /* margin-top: 10rem; */
    justify-content: center;
    padding: 4rem 2rem;
    justify-content: space-between;

}
.img-service-section{
    width: 48%;
    height: 24rem;
    background-image: url('../../assets/images/pngwing.png'); 
    background-size:cover;
    background-position: center;
    background-repeat: no-repeat;
}




.text-service-section{
    width: 48%;
    display: flex;
    justify-content: center;
    /* align-items: center; */
    flex-direction: column;
    
}
.text-service-section h2{
    color: #3949AC;
    font-size: 32px;
    
}
.text-service-section p{
    font-size: 24px;
    line-height: 1.5;
    color: rgb(44, 45, 48);
}








/* services boxes */
.explore-services {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    margin-top: 4rem;
    /* height: 100vh; */
    /* margin-top: 5rem; */
}

 .explore-services h1{
    font-size: 3.5rem;
    color: #3949AC;
    font-weight: 400;
 }
 .all-explore-services{
    display: flex;
    flex-direction: row;
    padding: 1rem 2rem;
    justify-content: space-around;
    margin-top: 5rem;
    /* background-color: #3949AC; */
    box-shadow: 0px -4px 8px rgba(29, 59, 255, 0.493), /* Top shadow */
    0px 4px 8px rgba(29, 59, 255, 0.493); 
   
 }
.explore-text-box {
    width: 40%;
    display: flex;
    /* justify-content: center; */
    flex-direction: column;
    /* align-items: center; */
    padding: 1rem 0;
    
}
.explore-text-box h2{
    font-size: 32px;
    font-weight: 400;
    color: #3949AC;
    /* color: #fff; */
}
.explore-text-box p{
    font-size: 20px;
    line-height: 1.3;
    color: rgb(44, 45, 48);
    /* color: #fff; */
    /* text-align: center; */
}
.offer-services {
    margin-top: 20px;
    
}
.offer-services h3{
    font-size: 24px;
    font-weight: 400;
    /* color: rgb(44, 45, 48); */
    /* color: #fff; */
    color: #3949AC;
    /* text-align: center; */
    
}

.tick-list li {
    list-style: none; /* Remove default bullet */
    position: relative;
    padding-left: 20px;
    font-size: 20px;
    line-height: 1.3;
    color: rgb(44, 45, 48);
    /* color: #fff; */
    /* color: rgb(29, 58, 255);  */


}

.tick-list li::before {
    content: "✓";
    color:#fff;
    font-size: 16px; 
    position: absolute;
    left: 0;
    top: 0;
    background-color: #3949AC;
    /* border: 14px solid rgb(14, 13, 13); */
    border-radius: 50%;
    padding: 0 2px;
    margin-top: 5px;
    /* margin-bottom: 13px; */
}

.explore-img-box{
    width: 40%;
    height: 22rem;
    background-image: url('../../assets/images/onpage.jpg'); 
    background-size:cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 15px;

    margin-top: auto;
    margin-bottom: auto;
}
.explore-reverse{
    flex-direction: row-reverse;
}
.offpage{
    background-image: url('../../assets/images/offpage.jpg'); 
}
.technical-box{
    background-image: url('../../assets/images/techniccal.jpg'); 
}
.keyword-box{
    background-image: url('../../assets/images/keyword.jpg'); 
}




@media screen and (max-width: 768px){
    .main-content-services{
        flex-direction: column-reverse;
    }
    .img-service-section{
        width: 100%;
    }
    .text-service-section {
        width: 100%;     
    }
    .text-service-section h2{
        text-align: center;
        font-size: 24px;
    }
    .text-service-section p{
        text-align: center;
        font-size: 16px;
    }

    /* more services */
    .all-explore-services {
        flex-direction: column;
    }
    .explore-text-box{
        width: 100%;
        text-align: center;
    }
    .explore-img-box {
        width: 100%;
    }
    .tick-list{
        display: flex;
        flex-direction: column;
        /* justify-content: start; */
        align-items: center;
        align-self: center;

    }

    .content-dev-seo h1 {
        font-size: 24px;
    }
    .content-dev-seo h1::first-letter {
        font-size: 38px;
        padding-top: 4rem;

    }
    .content-dev-seo p {
        font-size: 16px;
        padding: 1rem 2rem 0 2rem;
        
    }
    .main-seo a {
        padding: 0.5rem 1rem;
        font-size: 16px;
    }
    .services-section h1{
        font-size: 2rem;
    }
    .explore-text-box h2 {
        font-size: 24px;
    }
    .explore-text-box p {
        font-size: 16px;
    }
    .offer-services h3{
        font-size: 20px;
    }
    .tick-list li{
        font-size: 16px;
    }
    .tick-list li::before {
        font-size: 12px;
    }
}
