#etihad-developers {
    position: relative;
    width: 100%;
    height:85vh;
    /* overflow: hidden; */
    display: flex;
    justify-content: center;
    align-items: center;



    /* position: relative; */
    /* width: 100%; */
    /* height: 100vh;  */
    background-image: url('../assets/images/about-us.jpg'); /* Replace with the actual image path */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
#etihad-developers::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 1;
    /* color: rgb(29,58,255); */
}
.year{
    font-size: 2.5rem;
   
}

.video-background-video {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100vw;
    height: 56.25vw; 
    min-height: 100vh;
    min-width: 177.78vh;
    transform: translate(-50%, -50%);
    object-fit: cover;
    pointer-events: none;
}

.content-developer {
    position: relative;
    z-index: 1;

    color: white;
    text-align: center; 
    text-shadow: 4px 3px 4px rgb(29,58,255/ 50%);
    z-index: 3;
}

.video-center {
    padding: 200px 100px 100px 100px;
}
.content-developer h1 {
    font-size: 60px;
    text-transform: uppercase;
    font-weight: 400;
    line-height: 1;
    color: #fff;
}
.content-developer h1::first-letter {
    font-size: 100px;
    color: #fff;
}
.content-developer-40 span {
    font-size: 100px;
    color: #e4a73f;
}
.content-developer p {

    font-size: 18px;
    padding: 0 15px 30px 15px;
}
#contact-us-p{

    padding-top: 0;
    padding-bottom: 1.5rem;
}

.contactus-head h3{
    font-size: 50px;
    font-weight: 400;
}
@media (max-width: 1200px) {

}

@media (max-width: 768px) {
    .content-developer h1 {
        font-size: 2rem;
    }
    
    .content-developer p {
        
        font-size: 1rem;
    }
}
@media (max-width: 565px) {
    .content-developer h1 {
        font-size: 1.5rem;
    }

    .content-developer-40 span {
        font-size: 60px;
    }
    
    .content-developer p {
        font-size: 0.875rem;
        padding-top: 20px;
    }
    .content-developer h1::first-letter {
        font-size: 60px;
    }
}







#projects-developer{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.develpers-text{
    background-color:rgb(29,58,255) !important;
    display: flex;
    position: absolute;
    font-size: 4;
    width: 60%;
    padding: 40px;
    justify-content: center;
    align-items:center;
    border-radius: 30px;
    text-align: center;
    z-index: 1;
    
}
.develpers-text p{
    font-family: 'Times New Roman', Times, serif;
    color: #e8f3f3 !important;
    font-size: 1.1rem;
    line-height: 1.6;
    font-weight: 500;
    display: flex;
    align-self: center;
    justify-self: center;
    margin-bottom:0 !important;
    
}
@media screen and (max-width: 830px) {
    .develpers-text{
        width: 80%;
    }
}
@media screen and (max-width: 630px) {
    .develpers-text{
        width: 90%;
        padding: 20px;
        line-height: 1;
        margin-top: 0;
    }
    .develpers-text p{
        line-height: 1.2;
    }
}
@media screen and (max-width: 430px) {
    .develpers-text{
        padding: 13px;
    }
    .develpers-text p{
        font-size: 1rem;
        line-height: 1;
    }
} 

